tbody,
td,
tfoot,
th,
thead,
tr {
  border: 2px solid black;
}

.medium-center {
  font-size: medium;
  text-align: center;
}

.font {
  font-size: medium;
  display: flex;
  justify-content: space-between;
}

.btn {
  background-color: white;
  border-radius: 2rem;
  margin-right: 0.5rem;
}

.btn:hover {
  border: 2px solid black;
}

strong {
  font-weight: bolder;
  margin-left: 0.5rem;
}

.heading {
  display: flex;
  align-items: center;
}